
@media (max-width: 1919px) {
  .colors__title {
    margin-bottom: 20px;
  }

  .colors__text {
    margin-bottom: 20px;
  }
}

@media (max-width: 1599px) {
  .colors {
    padding-top: 100px;
  }

  .colors__row {
    flex-direction: column;
    align-items: center;
  }

  .colors__column-main {
    flex: 0 1 auto;
    margin-bottom: 80px;
  }

  .colors__insrument-row {
    justify-content: center;
  }

  .colors__column-palitra {
    width: calc(100% - 100px);
    max-width: 820px;
    min-height: 557px;
  }

  label{
  width: 50px;
  height: 50px;
}

  .colors__palitra-item {
    width: 50px;
    height: 50px;
  }
}

@media (max-width: 1365px) {
  
}

@media (max-width: 1023px) {
  .colors {
    padding-top: 80px;
  }

  .colors__instrument-block {
    padding-left: 0px;
  }

  .colors__instrument-item {
    padding-left: 25px;
  }

  .colors__instrument-item {
    background: #fff !important;
  }

  .colors__column-palitra {
    width: calc(100% - 50px);
    min-height: 517px;
  }

  .colors__palitra-text {
    margin-bottom: 20px;
  }

  .colors__palitra-item {
    width: 40px;
    height: 40px;
  }
}

@media (max-width: 767px) {
  .colors {
    padding-top: 50px;
  }

  .colors__column-main {
    margin-bottom: 10px;
  }

  .colors__insrument-row {
    flex-direction: column;
  }

  .colors__instrument-column_1 {
    max-width: 100%;
    margin: 0;
  }

  .colors__instrument-block_help {
    margin-bottom: 30px !important;
  }

  .colors__button {
    width: 90%;
  }

  .colors__column-palitra {
    display: none;
  }
}