.text {
  display: inline-block;
}

.text_effect_underline-white:after {
  display: block;
  position: relative;
  content: "";
  height: 1px;
  width: 100%;
  background-color: #fff;
}
.text_effect_underline-blue:after {
  display: block;
  position: relative;
  content: "";
  height: 1px;
  width: 100%;
  background-color: #0c59a5;
}

.text_effect_hover-underline-white:after {
  display: block;
  position: relative;
  left: 50%;
  content: "";
  height: 1px;
  width: 0%;
  background-color: #fff;
  transition: 0.3s ease-in-out;
}

.text_effect_hover-underline-white:hover:after {
  width: 100%;
  transform: translateX(-50%);
}

.text_effect_hover-underline-black:after {
  display: block;
  position: relative;
  left: 50%;
  content: "";
  height: 1px;
  width: 0%;
  background-color: #000;
  transition: 0.2s ease-in-out;
}

.text_effect_hover-underline-black:hover:after {
  width: 100%;
  transform: translateX(-50%);
}

.text_size_bold {
  font-weight: 700;
}

.text__title {
  font-weight: 900;
  font-size: 40px;
 
}
.text__text {
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
}

@media (max-width: 1599px) {
  .text__title{
    font-size: 32px;
  }
}

@media (max-width: 767px) {
  .text__title{
    font-size: 25px;
  }
}