
@media (max-width: 1919px) {
  .intro__photo-main {
    width: 420px;
    height: 420px;
  }
  
  .img__comp-slider{
    height: 540px;
  }

  .intro__column:first-child {
    margin-right: 40px;
  }
}

@media (max-width: 1599px) {
  .intro__row {
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
  }

  .intro__photo-main {
    width: 80%; 
  }

  .intro__column:first-child {
    margin: 0 0 100px 0;
  }

  .intro__title {
    align-self: center;
  }

  .intro__slider {
    width: 80%;
  }

  .intro__slider-row { 
    align-self: center;
    margin-left: -150px;
  }
}

@media (max-width: 1365px) {  
}

@media (max-width: 1023px) {
  .intro__photo-main { 
    height: 320px;
  }

  .img__comp-slider{
    height: 400px;
  }
}

@media (max-width: 767px) {
  .intro {
    padding-top: 50px;
  }

  .intro__title {
    max-width: 80%;    
  }

  .intro__text-slider{
    display: none !important;
  }

}