
@media (max-width: 1599px) {
  .map {
    padding-top: 100px;
  }

  .map__google-map {
    height: 400px;
    overflow: auto;
  }

  .map__list {
    -webkit-box-flex: 0;
        -ms-flex: 0 0 400px;
            flex: 0 0 400px;
  }
}

@media (max-width: 1023px) {
  .map {
    padding-top: 80px;
  }

  .map__title {
    margin-bottom: 40px;
  }

  .map__list {
    -webkit-box-flex: 0;
        -ms-flex: 0 0 280px;
            flex: 0 0 280px;
    padding: 20px 20px;
  }

  .map__list-title {
    font-size: 22px;
  }
}

@media (max-width: 767px) {
  .map {
    padding-top: 50px;
  }

  .map__blue-area {
    height: 220px;
  }

  .map__google-map {
    height: 300px;
  }

  .map__iframe {
    width: 100%;
  }

  .map__list {
    position: absolute;
    top: -500px;
    right: 0px;
    width: 280px;
    opacity: 0;
    visibility: hidden;
  }

  .map__menu-btn {
    display: block;
  }
  .map__blue-area{
    display: none;
  }
}