.blue-area {
  background: #0c59a5;
  height: 420px;
  width: 60%;
  position: absolute;
  z-index: -2;  
}
.blue-area_right {
  bottom: 0;
  right: 0;
}

.blue-area_left {
  bottom: -50px;
  left: 0;
}

.blue-area_width {
  width: 100%;
}

@media (max-width: 1599px) {
  .blue-area {
    width: 100%;  
  }    
}