
.social {
  padding: 140px 0 0 0;
  position: relative;
}

.social__blue-area {
  position: absolute;
  top: 140px;
}

.social__conteiner {
  padding: 40px 40px 0;
}

.social__title {
  color: #fff;
  max-width: 260px;
  margin: 0 0 30px 0;
}

.social__text {
  color: #fff;
  max-width: 260px;
  margin: 0 0 40px 0;
}

.social__items {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 50px;
}

.social__item {
  -webkit-box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.43);
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.43);
}

.social__item iframe {
  width: 100%;
  height: 400px;
}
