
.header {
  width: 100%;
  margin: 0 auto;
  height: 100vh;
  position: relative;
  background: url("../../images/header-main.jpg") center/cover no-repeat;
  color: #fff;
  font-size: 18px;
  font-weight: 400;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  overflow: hidden; 
}

.header__left {
  background: #0c59a5;
  width: 135px;
  align-self: stretch;
  height: 100%;
}

.header__left-container {
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.header__left-cross {
  position: relative;
  width: 100%;
  height: 150px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  justify-content: center; 
  align-items: center;
  opacity: 0;
  visibility: hidden;
  transition: 0.5s;  
}

.header__left-cross-button{
position: relative;
width: 30px;
height: 30px;
}

.header__left-cross_show {
  opacity: 1;
  visibility: visible;
}

.header__left-cross-button:hover {
  cursor: pointer;
}

.header__left-column{
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.header__left-item {
  width: 100%;
  position: relative;
  margin: 0 0 10px 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  transition: 0.5s;
  background: #0c59a5;
  flex: 0 1 80px;
  overflow: hidden;
}

.header__left-item:last-child {
  margin-top: auto;
}
.header__left-item:last-child .header__left-text {
  font-size: 12px;
}

.header__left-item:after {
  content: "";
  width: 40px;
  height: 2px;
  background-color: #0a5196;
  position: absolute;
  bottom: 0px;
  left: 50%;
  transform: translateX(-50%);
}

.header__left-item:last-child:after {
  display: none;
}

.header__left-item:before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 80px;
  height: 80px;
  background: radial-gradient(circle closest-side at center, #3382cf, transparent);
  opacity: 0;
  transition: 0.5s;
}

.header__left-item:hover:before {
  opacity: 1;
  cursor: pointer;
}

.header__left-item:hover {
  cursor: pointer;
}

.header__left-item-pdf{  
  margin-top: auto;
}

.header__left-item-img {
  width: 40%;
  height: 100%;
  max-height: 35px;
  margin-bottom: 10px;
  z-index: 1;
}

.header__left-item-img-pdf{
  width: 35px;
  min-height: 35px;
  margin-bottom: 15px;
  z-index: 1;
}

.header__left-text {
  padding: 0 10px;
  font-size: 16px;
  margin-bottom: 5px;
  text-align: center;
  color: #fff;
  z-index: 1;
  font-weight: 500;
  min-height: 32px;
  
}

.header__left-text-pdf {
  font-size: 14px;
  font-weight: 400;
  opacity: 0.5;
  transition: 0.5s;
}

.header__left-item-pdf:hover .header__left-text-pdf {
  opacity: 1;
}



/* sub menu left =============================== */

.header__left-submenu-items {
  position: absolute;
  opacity: 0;
  visibility: hidden;
  transition: 0.5s;
  z-index: 1;
  left: 150px;
  width: 330px;
  top: 150px;
}

.header__left-submenu-items_opened {
  opacity: 1;
  visibility: visible;
}

.header__left-submenu-item {
  display: block;
  font-size: 18px;
  font-weight: 700;
  padding: 40px 40px;
  background: rgba(12, 89, 165, 0.95);
  margin-bottom: 50px;
  border-radius: 5px;
}

.header__left-submenu-text:hover {
  cursor: pointer;
}

/* top ================================================================================================= */

.header__top {
  width: 100%;
  padding: 50px 50px 0 50px;
}

.header__sub-menu-top {
  display: block;
  position: fixed;
  top: -100px;
  right: 0;
  width: 100vw;
  padding: 18px;
  background: rgba(12, 89, 165, 0.95);
  z-index: 50;
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.43);
  transition: 0.5s;
  min-width: 320px;
  display: flex;
  justify-content: flex-start;
}

.logo_place_sub-menu-top {
  width: 100px;
}

.header__top-container {
  width: 100%;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 275px 1fr 400px;
  gap: 50px;
}

.logo_place_header {
  height: 77px;
}

.header__top-column_place_center-btn{
  display: none;
}

.header__top-column_place_center {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  max-width: 1300px;
  z-index: 10;
}

/* row 1 =================================== */

.header__top-row1 {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
}

.header__top-button {
  font-size: 14px;
  font-weight: 700;
  background: #d72124;
  padding: 10px 20px 10px 20px;
  transition: 0.5s;
  color: #fff;
}

.header__top-button:hover {
  cursor: pointer;
  background: #b21d1f;
}

.header__top-basket {
  position: relative;
  width: 25px;
  height: 25px;
  background: url('../../images/logo/header-basket.png') center/contain no-repeat;
}

.header__top-basket:hover {
  cursor: pointer;
}

.header__top-basket-quantity {
  width: 20px;
  height: 20px;
  position: absolute;
  top: -10px;
  left: 18px;
  font-size: 12px;
  font-weight: 400;
  line-height: 20px;
  text-align: center;
  background: #d72124;
  border-radius: 50%;
  color: #fff ;
  
}

.header__top-city {  
  width: 130px;
  z-index: 5;  
  position: relative;
  top: 0px;
  width: 135px;
  height: 18px;
}

.header__top-city:hover {
  cursor: pointer;  
}

.header__top-city-items{
  position: absolute;
  top: -15px;
  padding: 15px;
  transition: 0.5s;
}

.header__top-city-items_opened{
  background: rgba(12, 89, 165, 0.95);
  border-radius: 10px;
}


.header__top-city-sub-item{
  display: none;
}

.header__top-city-arrow{
  top: 10px;
}

.header__top-city-items_opened .header__top-city-sub-item{
  display: block;
}


.header__top-city-text {
  padding-left: 20px;
  margin-bottom: 15px;
}

.header__top-phone {
  background: url("../../images/logo/header-trubka.svg") left no-repeat;
  padding-left: 20px;
}

.header__top-phone:hover {
  cursor: pointer;
  
}

.header__top-lang {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  position: relative;
}

.header__top-lang-row {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  background: none;
}

.header__top-lang-row_2 {
  width: 100%;
  position: absolute;
  top: 20px;
  left: 0;
  display: none;
  background: rgba(7, 47, 84, 0.8);
  transition: 0.5s;
  padding-left: 5px;
}

.header__top-lang-row-2_opened {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.header__top-lang-img {
  padding: 3px;
  width: 20px;
  height: 20px;
}

.header__top-lang-text {
  color: #fff;
  padding: 3px 3px 3px 3px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  transition: 0.5s;
  text-transform: uppercase;
}

.header__top-lang-text-row-1 {
  padding-right: 20px;
}

.header__top-lang:hover {
  cursor: pointer;
}

.header__top-lang:hover .header__top-lang-text-row-1 {
  background: rgba(7, 47, 84, 0.8);
  
}

.header__top-lang-arrow {
  position: relative;
  top: 0;
  left: -17px;
}

.header__top-lang-text-row-2 {
  display: block;
  font-size: 12px;
  font-weight: 300;
  text-align: center;
  width: 100%;
}

.header__top-lang-show {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

/* row-2 =================================================== */

.header__top-row2 {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__top-nav-item {
  position: relative;
  cursor: pointer;
}

.header__top-nav-link {
  color: #fff;
  display: inline-block;
}

/* submenu top ========================= */

.header__top-submenu-wrapper {
  position: relative;
  visibility: hidden;
  opacity: 0;
  position: absolute;
  top: 20px;
  min-width: 210px;
  padding-top: 20px;
  transition: 0.5s;
  z-index: 1;
}

.header__top-nav-item:hover .header__top-submenu-wrapper {
  visibility: visible;
  opacity: 1;
}

.header__top-submenu-triangle {
  position: absolute;
  top: 15px;
  left: 15%;
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
  border-bottom: 5px solid #154b97;
}

.header__top-submenu {
  background: rgba(12, 89, 165, 0.95);
  padding: 40px;
  margin-left: -50%;
  border-radius: 5px;
}

.header__top-submenu-item {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin: 0 0 25px 0;
}

.header__top-submenu-item:last-child {
  margin: 0;
}

.header__top-submenu-text {
  color: #fff;
  text-decoration: none;
  display: inline-block;
  font-weight: 500;
  position: relative;
}

/* banner ============================= */

.header__top-column_place_right {
  position: relative;
}

.header__top-banner {
  background: #d72124;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  flex-direction: column;
  padding: 10px 40px 10px 40px;
  height: 75px;
  justify-content: center;
  position: relative;
}

.header__top-banner:hover {
  cursor: pointer;
}

.header__top-banner-title {
  margin: 0 0 10px 0;
  padding: 0;
  font-size: 27px;
  font-weight: bold;
}

.header__top-banner-arrow {
  position: absolute !important;
  top: 20px;
  right: 30px;
  z-index: 2;
  transition: 0.5;
}

/* sub-banner============== */

.header__top-sub-banner {
  width: 100%;
  position: absolute;
  top: -200px;
  left: 0;
  opacity: 0;
  visibility: hidden;
  padding: 30px 35px;
  background: #fff;
  transition: 0.8s;
}

.header__top-sub-banner-active {
  top: 0;
  opacity: 1;
  visibility: visible;
}

.logo_place_banner {
  height: 25px;
  margin: 0 0 10px 0;
}

.header__top-sub-banner-title {
  margin: 0 0 10px 0;
  font-size: 27px;
  color: #0c59a5;
}

.header__top-sub-banner-subtitile {
  color: #0c59a5;
  margin: 0 0 20px 0;
}

.header__top-sub-banner-img {
  width: calc(100% + 70px);
  transform: translateX(-35px);
  margin-bottom: 10px;
  height: 150px;
  background: url('../../images/header-banner.png') center/contain no-repeat;
}

.header__top-sub-banner-list {
  color: #000;
  margin-bottom: 20px;
}

.header__top-sub-banner-list-item {
  background: url("../../images/logo/header-banner-galochka.png") left no-repeat;
  padding-left: 20px;
  margin-bottom: 10px;
}

.header__top-sub-banner-button {
  background: #d72124;
  color: #fff;
  padding: 10px 20px;
  display: inline-block;
  margin: 0 20px 10px 0;
  transition: 0.5s;
}

.header__top-sub-banner-button:hover {
  background: #b21d1f;
}

.header__top-sub-banner-price {
  color: #0c59a5;
  display: inline-block;
  font-weight: 700;
}

.header__top-column_place_right:hover .arrow_direct_up:before {
  transform: rotate(45deg);
}

.header__top-column_place_right:hover .arrow_direct_up:after {
  transform: rotate(-45deg);
}

/* search =========================================================================================== */

.header__search {
  background: rgba(21, 75, 151, 0.95);
  width: 40%;
  height: 45px;
  position: absolute;
  top: 80%;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 10px;
}

.header__search-form{
  position: relative;
}

.header__search-submit {
  position: relative;
  top: 5px;
  background: url("../../images/logo/header-search.png") 15px 50% no-repeat;;
  cursor: pointer;
  width: 35px;
  height: 35px;
}

.header__search-input {
  width: 75%;
  background: rgba(21, 75, 151, 0.01);
  font-size: 22px;
  color: #fff;
  position: relative;
  top: -6px;
  left: 20px;
}

.header__search-cross {
  position: absolute;
  background: none;
  top: 7px;
  right: 15px;
  width: 30px;
  height: 30px;
}

