

@media (max-width: 1599px) {
  .social {
    padding-top: 100px;
  }

  .social__blue-area {
    top: 100px;
  }

  .social__items {
    grid-template-columns: repeat(2, 1fr);
  }

  .social__item iframe {
    height: 300px;
  }
}

@media (max-width: 1023px) {
  .social {
    padding-top: 80px;
  }

  .social__blue-area {
    top: 80px;
  }

  .social__conteiner {
    padding: 40px 0 0;
  }
}

@media (max-width: 767px) {
  .social {
    padding-top: 60px;
  }

  .social__blue-area {
    top: 60px;
  }

  .social__items {
    grid-template-columns: 1fr;
  }

  .social__item iframe {
    height: 250px;
  }
}