
.footer {
  padding: 140px 0 10px 0;
}

.footer__row-1 {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: start;
  margin: 0 0 20px 0;
}

.footer__nav-1 {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
}

.footer__nav-item {
  margin: 0 0 20px 0;
  display: inline-block;
}

.footer__nav-link {
  display: inline-block;
  font-size: 16px;
  font-weight: 700;
  color: #000;
}

.footer__nav-2 {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}

.footer__title {
  font-size: 16px;
  font-weight: 900;
  margin: 0 0 20px 0;
}

.footer__text {
  font-size: 16px;
  margin: 0 0 20px 0;
  line-height: 20px;
}

.footer__logo-row {
  margin: -15px 0 20px 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.footer__logo-row-item {
  width: 35px;
  height: 35px;
  display: inline-block;
  margin-right: 5px;
}

.footer__logo-row-item-1 {
  background: url("../../images/footer/Facebook.png") center/cover no-repeat;
}

.footer__logo-row-item-2 {
  background: url("../../images/footer/Instagram.png") center/cover no-repeat;
}

.footer__logo-row-item-3 {
  background: url("../../images/footer/Twitter.png") center/cover no-repeat;
}

.footer__logo-row-item-4 {
  background: url("../../images/footer/Vimeo.png") center/cover no-repeat;
}

.footer__logo-row-item-5 {
  background: url("../../images/footer/YouTube.png") center/cover no-repeat;
}

.footer__app-row {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 10px;
}

.footer__app-row-item {
  width: 150px;
  height: 50px;
  display: inline-block;
  margin: 0 5px 5px 0;
}

.footer__app-row-row-item-1 {
  background: url("../../images/footer/app.png") center/cover no-repeat;
}

.footer__app-row-row-item-2 {
  background: url("../../images/footer/google.png") center/cover no-repeat;
}

.footer__form {
  width: 325px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}

.footer__form-item {
  border: 1px solid #d9d9d9;
  width: 100%;
  padding: 10px 10px;
  margin: 0px 0 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.footer__form-item_error {
  border: 1px solid red;
}

.footer__form-text-error {
  display: inline-block;
  height: 30px;
  width: 100%;
  margin: 0;
  padding: 0;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 15px;
  color: #ff0000;
}

.footer__form-text {
  color: #909090;
  line-height: 22px;
  margin: 0 0 10px 0;
}

.footer__form-link {
  display: inline-block;
  margin: 0 0 10px 0;
}

.footer__form-link a {
  color: #006bb6;
  border-bottom: 1px dotted #006bb6;
}

.footer__button {
  color: #fff;
  background: #006bb6;
  padding: 10px 20px;
  transition: 0.5s;
}

.footer__button:hover {
  cursor: pointer;
  background: #154d99;
}

.footer__button_invalid {
  background: grey;
}

.footer__button_invalid:hover {
  background: grey;
  cursor: auto;
}

.footer__row-2 {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  margin: 0;
}

.footer__row-2-text {
  color: #939393;
}

.footer__row-2-text a {
  color: #939393;
}

.line {
  background: url("../../images/footer/line.png") center/cover no-repeat;
  width: 100%;
  height: 110px;
  margin: 0 0 90px 0;
}