.cross {
  display: block;
}

.cross:before,
.cross:after {
  content: "";
  display: inline-block;
  position: absolute;
  height: 1px;
  background-color: #fff;
}
.cross:before {
  transform: rotate(45deg);
  top: 4px;
}
.cross:after {
  transform: rotate(-45deg);
  top: 4px;
}

.cross_size_m:before,
.cross_size_m:after {
  width: 15px;
}

.cross_size_l:before,
.cross_size_l:after {
  width: 20px;
  top: 14px;
  right: 5px;
}
