
.about {
  padding: 140px 0 0 0;
}

.about__container {
  min-height: 350px;
  position: relative;
}

.about__title {
  margin: 0 0 35px 0;
}

.about__text {
  max-width: 575px;
  margin: 0 0 45px 0;
}

.about__bg-image {
  position: absolute;
  bottom: 0;
  right: 0;
  height: 100%;
  max-height: 600px;
  width: calc(100% - 600px);
  background: url("../../images/about/about.jpg") center/cover no-repeat;
}

.about__links {
  margin: 0 0 70px 0;
}

.about__link-item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 0 50px 20px 0;
}

.about__link-item:last-child {
  margin: 0 50px 0 0;
}

.about__link {
  color: #0c59a5;
  display: block;
  font-size: 18px;
  font-weight: 700;
  margin: 0 40px 0 0;
  position: relative;
}

.about__items {
  width: 830px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: start;
  margin: 0 0 40px;
}

.about__item {
  z-index: 2;
  color: #0c59a5;
  background: #fff;
  width: 245px;
  height: 200px;
  padding: 0 0 0 20px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.43);
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.43);
  margin: 0 20px 20px 0;
}

.about__item-title {
  margin: 0 0 20px 0;
  font-size: 74px;
  font-weight: 700;
}

.about__item-title_small {
  font-size: 30px;
}

.about__item-text {
  max-width: 100px;
  font-size: 14px;
  font-weight: 700;
}

.about__anim-item-n-1 {
  content: 0;
}

.about__anim-item-n-2 {
  content: 0;
}

.about__anim-item-n-3 {
  content: 0;
}

.about__button {
  max-width: 545px;
  display: inline-block;
  text-align: center;
  font-size: 18px;
  line-height: 28px;
}

