
.map {
  position: relative;
  padding: 140px 0 0 0;
}

.map__title {
  max-width: 570px;
  margin: 0 0 70px 0;
}

.map__link {
  color: #0c59a5;
  display: inline-block;
}

.map__link:after {
  display: block;
  content: "";
  height: 2px;
  width: 100%;
  background-color: #0c59a5;
  -webkit-transition: 0.2s ease-in-out;
  -o-transition: 0.2s ease-in-out;
  transition: 0.2s ease-in-out;
}

.map__link:hover:after {
  opacity: 0;
}

.map__google-map {
  position: relative;
  width: 100%;
  height: 500px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.43);
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.43);
  background: #fff;
}

.map__iframe {
  -webkit-box-flex: 0;
      -ms-flex: 0 1 1145px;
          flex: 0 1 1145px;
}

.map__list {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 500px;
          flex: 0 0 500px;
  padding: 40px 50px;
  overflow: auto;
  position: relative;
  z-index: 10;
  background: #fff;
  -webkit-transition: 0.7s;
  -o-transition: 0.7s;
  transition: 0.7s;
}

.map__list_active {
  top: 0px !important;
  visibility: visible !important;
  opacity: 1 !important;
  height: 280px !important;
}

.map__list-item {
  margin: 0 0 55px 0;
}

.map__list-title {
  font-size: 32px;
  font-weight: bold;
  color: #0c59a5;
  margin: 0 0 15px 0;
}

.map__list-text {
  font-size: 17px;
  color: #7fa8d0;
  margin: 0 0 25px 0;
}

.map__list-open {
  font-size: 16px;
  color: black;
  text-decoration: underline;
  display: block;
  margin: 0 0 20px 0;
}

.map__list-open p {
  padding-left: 20px;
}

.map__list-map {
  padding-left: 20px;
  font-size: 16px;
  color: black;
  text-decoration: underline;
  display: block;
  margin: 0 0 20px 0;
  background: url("../../images/map/metka.png") no-repeat;
}

.map__menu-btn {
  background-color: #fff;
  display: none;
  position: absolute;
  top: 0px;
  right: 0px;
  font-size: 16px;
  color: black;
  padding: 10px 20px;
  -webkit-box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.43);
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.43);
  width: 280px;
}
.map__menu-btn:hover {
  cursor: pointer;
}
.map__menu-btn:hover p {
  text-decoration: underline;
}

.map__menu-btn p {
  padding-left: 20px;
}

#map {
  height: 300px;
  width: 600px;
}
