
@media (max-width: 1599px) {
  .about {
    padding-top: 100px;
  }
}

@media (max-width: 1365px) {
  .about__bg-image {
    position: relative;
    width: 100%;
    height: 350px;
    margin-bottom: 30px;
  }
}

@media (max-width: 1023px) {
  .about {
    padding-top: 80px;
  }

  .about__items {
    width: 100%;
  }
}

@media (max-width: 767px) {
  .about {
    padding-top: 60px;
  }

  .about__bg-image {
    display: none;
  }

  .about__items {
    display: none;
  }
}