.arrow {
  position: relative;
  background-color: #fff;
}

.arrow_direct_douwn:before,
.arrow_direct_douwn:after {
  content: "";
  width: 8px;
  height: 1px;
  background-color: #fff;
  position: absolute;
  top: 0px;
}
.arrow_direct_douwn:before {
  transform: rotate(45deg);
  left: 0;
}
.arrow_direct_douwn:after {
  transform: rotate(-45deg);
  left: 5px;
}

.arrow_direct_up:before,
.arrow_direct_up:after {
  content: "";
  width: 8px;
  height: 1px;
  background-color: #fff;
  position: absolute;
  top: 0px;
  transition: 0.2s;
}
.arrow_direct_up:before {
  transform: rotate(-45deg);
  left: 0;
}
.arrow_direct_up:after {
  transform: rotate(45deg);
  left: 5px;
}

/* =================== */
.arrow_direct_left {
  width: 20px;
  height: 20px;
  display: inline-block;
  position: absolute;
  top: 0;
  margin-right: 15px;
  transition: 1s;
}
.arrow_direct_left span {
  width: 1px;
  height: 1px;
  background: #000;
  display: inline-block;
  position: absolute;
  top: 8px;
  left: 2px;
  transition: 1s;
}

.arrow_direct_left:before,
.arrow_direct_left:after {
  content: "";
  display: block;
  width: 10px;
  height: 1px;
  background-color: #000;
  position: absolute;
  top: 8px;
  left: 0px;
  transition: 1s;
}
.arrow_direct_left:before {
  transform: rotate(-45deg);
  top: 4px;
}
.arrow_direct_left:after {
  transform: rotate(45deg);
  top: 11px;
}

.arrow_direct_left:hover {
  width: 112px;
  transform: translateX(-92px);
}
.arrow_direct_left:hover span {
  width: 92px;
}
.arrow_direct_left:hover:before,
.arrow_direct_left:hover:after {
  right: 91px;
}

/* ================ */
.arrow_direct_right {
  width: 20px;
  height: 20px;
  display: inline-block;
  position: absolute;
  top: 0;
  margin-left: 15px;
  transition: 1s;
}
.arrow_direct_right span {
  width: 1px;
  height: 1px;
  background: #000;
  display: inline-block;
  position: absolute;
  top: 8px;
  left: 8px;
  transition: 1s;
}

.arrow_direct_right:before,
.arrow_direct_right:after {
  content: "";
  display: block;
  width: 10px;
  height: 1px;
  background-color: #000;
  position: absolute;
  top: 8px;
  left: 0px;
  transition: 1s;
}
.arrow_direct_right:before {
  transform: rotate(45deg);
  top: 4px;
}
.arrow_direct_right:after {
  transform: rotate(-45deg);
  top: 11px;
}

.arrow_direct_right:hover {
  width: 100px;
}
.arrow_direct_right:hover span {
  width: 92px;
}
.arrow_direct_right:hover:before,
.arrow_direct_right:hover:after {
  left: 91px;
}

.arrow_douwn:before,
.arrow_douwn:after {
  content: "";
  width: 8px;
  height: 1px;
  background-color: #000;
  position: absolute;
  top: 7px;
}
.arrow_douwn:before {
  transform: rotate(45deg);
  left: 0;
}
.arrow_douwn:after {
  transform: rotate(-45deg);
  left: 6px;
}

.arrow_right {
  width: 1px;
  height: 1px;
  background: #000;
  display: inline-block;
  position: absolute;
  top: 8px;
  margin-left: 15px;
  transition: 1s;
}

.arrow_right:before,
.arrow_right:after {
  content: "";
  display: block;
  position: absolute;
  width: 10px;
  height: 1px;
  background-color: #000;
  position: absolute;
  top: 0px;
  left: -8px;
  transition: 1s;
}
.arrow_right:before {
  transform: rotate(45deg);
  top: -4px;
}
.arrow_right:after {
  transform: rotate(-45deg);
  top: 4px;
}

a:hover .arrow_right {
  width: 100px;
}
a:hover .arrow_right:before,
a:hover .arrow_right:after {
  left: 91px;
}
