

@media (max-width: 1599px) {
  .footer {
    padding-top: 100px;
  }
}

@media (max-width: 1365px) {
  .footer__row-1 {
    -ms-flex-pack: distribute;
        justify-content: space-around;
  }

  .footer__column:nth-child(4) {
    display: none;
  }

  .footer__nav-2 {
    display: none;
  }
}

@media (max-width: 1023px) {
  .footer {
    padding-top: 80px;
  }
}

@media (max-width: 767px) {
  .footer {
    padding-top: 60px;
  }

  .footer__row-1 {
    flex-direction: column;
  }

  .footer__app-row {
    flex-direction: column;
  }

  .footer__row-2 {
    flex-direction: column;
  }

  .line {
    display: none;
  }
}