
.colors {
  position: relative;
  padding-top: 140px;
}

.colors__containter {
  z-index: 2;
}

.colors__row {
  display: flex;
  justify-content: flex-start;
}

.colors__column {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.colors__column-main {
  flex: 0 0 690px;
}

.colors__title {
  max-width: 600px;
  margin: 0 0 45px 0;
}

.colors__text {
  max-width: 600px;
  margin: 0 0 60px 0;
}

.colors__insrument-row {
  display: flex;
  justify-content: flex-start;
  margin: 0 0 40px 0;
}

.colors__instrument-column {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
}

.colors__instrument-column_1 {
  margin: 0 45px 0 0;
  max-width: 300px;
  flex: 0 0 50%;
}

.colors__instrument-block {
  display: flex;
  align-items: center;
  padding: 20px 25px 20px 25px;
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.43);
  color: #2a69a8;
  background: #fff;
  font-size: 16px;
  font-weight: 700;
  margin: 0 0 5px 0;
  min-height: 115px !important;
  width: 100%;
}

.colors__instrument-item {
  padding-left: 90px;
  margin: 0 0 25px 0;
  min-height: 50px;
  width: 100%;
}

.colors__instrument-item:hover {
  cursor: pointer;
}

.colors__instrument-column_1 .colors__instrument-item:last-child {
  margin: 0 0 0 0;
}

.colors__instrument-column_2 .colors__instrument-item:last-child {
  margin: 0 0 0 0;
}

.colors__instrument-block_instr {
  background: #fff url("../../images/colors/chat.png") 20px 50% no-repeat;
}

.colors__instrument-block_help {
  background: #fff url("../../images/colors/naushnik.png") 20px 50% no-repeat;
}

.colors__instrument-block-title {
  display: inline-block;
  font-size: 16px;
  font-weight: 700;
  color: #2a69a8;
  margin: 0 0 5px 0;
  text-decoration: underline;
}

.colors__instrument-block-text {
  font-size: 14px;
  color: #2a69a8;
  text-decoration: none;
}

.colors__instrument-item:hover .colors__instrument-block-title {
  text-decoration: none;
}

.colors__instrument-column_2 {
  margin: 0 20px 0 0;
  flex: 0 0 50%;
}

.colors__instrument-item_1 {
  background: url("../../images/colors/kraski.png") 0 50% no-repeat;
}

.colors__instrument-item_2 {
  background: url("../../images/colors/kraski.png") 0 50% no-repeat;
}

.colors__instrument-item_3 {
  background: url("../../images/colors/kraski.png") 0 50% no-repeat;
}

.colors__instrument-materials {
  display: block;
  color: #8bb0d4;
  font-size: 15px;
  font-weight: 700;
  text-align: center;
  padding-top: 60px;
  background: url("../../images/colors/materials_white.png") 50% 0 no-repeat;
  opacity: 0.7;
  transition: 0.5s;
  margin: 0 0px 60px 0;
}

.colors__instrument-materials:hover {
  color: white;
  cursor: pointer;
  opacity: 1;
}

.colors__instrument-color {
  display: inline-block;
  font-size: 15px;
  color: #8bb0d4;
  font-weight: 700;
  text-align: center;
  padding-top: 65px;
  background: url("../../images/colors/kraski_white.png") 50% 0 no-repeat;
  opacity: 0.7;
  transition: 0.5s;
  margin: 0 0px 60px 0;
}

.colors__instrument-color:hover {
  color: white;
  cursor: pointer;
  opacity: 1;
}

.colors__instrument-active {
  color: white;
  opacity: 1;
}

.colors__button {
  width: 545px;
  text-align: center;
  font-size: 18px;
  padding: 20px 0;
}

/* palitra ================================================================== */
.colors__column-palitra {
  display: grid;
  grid-template-columns: 100px 1fr;
  flex: 1 0 auto;
  gap: 10px;
  min-height: 617px;
}

.colors__instrument-column-palitra{
  overflow: hidden;
  position: relative;
  height: 100%;
}

.colors__instrument-column-palitra_btns {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  color: #fff;
  height: 100%;
}

.colors__instrument-palitra-item {
  display: block;
  color: #8bb0d4;
  font-size: 15px;
  font-weight: 700;
  text-align: center;
  opacity: 0.7;
  transition: 0.5s;
  margin: 0 0px 60px 0;
}

.colors__instrument-palitra-item:hover {
  color: white;
  cursor: pointer;
  opacity: 1;
}

.colors__instrument-materials {  
  padding-top: 60px;
  background: url("../../images/colors/materials_white.png") 50% 0 no-repeat;  
}

.colors__instrument-color {
  padding-top: 65px;
  background: url("../../images/colors/kraski_white.png") 50% 0 no-repeat;  
}

.colors__instrument-active {
  color: white;
  opacity: 1;
}

.colors__palitra {
  display: block;
  width: 100%;
  opacity: 0;
  visibility: hidden;
  position: absolute;
  top: 0;
  left: 100%;
  background: #f6f6f6;
  padding: 60px 60px 20px;
  transition: 0.5s;
}

.colors__palitra-text {
  font-size: 18px;
  font-weight: 700;
  margin: 0 0 40px 0;
}

.colors__palitra-main-colors {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin: 0 0 50px 0;
}

label{
  width: 70px;
  height: 70px;
}

input[type="radio"] {
  position: absolute;
  width: 1px; 
  height: 1px;
  overflow: hidden; 
  clip: rect(0 0 0 0);
} 

input[type="radio"]:checked + .colors__palitra-item{
  cursor: pointer;
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.7);
}

.colors__palitra-item:hover {
  cursor: pointer;
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.7);
}

.colors__palitra-item {
  width: 70px;
  height: 70px;  
  transition: 0.5s;
}

.colors__palitra-main-item_1 {
  background: #ff0000;
}

.colors__palitra-main-item_2 {
  background: #ffff02;
}

.colors__palitra-main-item_3 {
  background: #00ff01;
}

.colors__palitra-main-item_4 {
  background: #0000ff;
}

.colors__palitra-main-item_5 {
  background: #808080;
}

.colors__palitra-main-item_6 {
  background: #584e39;
}

.colors__palitra-shade-colors {
  display: flex;          
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
}

.colors__palitra-shade-colors-row {
  display: flex; 
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 50px;
}

.colors__palitra-shade-item_1 {
  background: #ffd1dc;
}

.colors__palitra-shade-item_2 {
  background: #fde90f;
}

.colors__palitra-shade-item_3 {
  background: #99ff99;
}

.colors__palitra-shade-item_4 {
  background: #30d5c8;
}

.colors__palitra-shade-item_5 {
  background: #d7d7d7;
}

.colors__palitra-shade-item_6 {
  background: #ffffff;
}

.colors__palitra-shade-item_7 {
  background: #fc5a8d;
}

.colors__palitra-shade-item_8 {
  background: #fcdd76;
}

.colors__palitra-shade-item_9 {
  background: #5da130;
}

.colors__palitra-shade-item_10 {
  background: #42abff;
}

.colors__palitra-shade-item_11 {
  background: #808080;
}

.colors__palitra-shade-item_12 {
  background: #f5f5dc;
}

.colors__palitra-button {
  margin: 0 0 0 0;
  color: #000;
  display: inline-block;
  font-size: 18px;
  background: #b1b1b1;
  padding: 20px 0;
  width: 100%;
  text-align: center;
  box-shadow: 0px -30px 20px 40px white;
  transition: 0.5s;
}

.colors__palitra-button:hover {
  background: #6c6c6c;
}

.colors__materials {
  display: block;
  position: absolute;
  top: 0;
  left: 100%;
  opacity: 0;
  visibility: hidden;
  background: url("../../images/colors/materials.jpg") 50% 50%/cover no-repeat;
  transition: 0.5s;
  width: 100%;
  height: 631px;
}

.colors__window-open {
  opacity: 1;
  visibility: visible;
  left: 0;
}
