
.intro {
  padding: 120px 0 0 0;
}

.intro__row {
  display: flex;
  justify-content: center;
  align-items: stretch;
}

.intro__column {  
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.intro__column:first-child {
  margin-right: 50px;
}

.intro__photo-main {
  width: 668px;
  height: 638px;
  position: relative;
}

.img__comp-img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.img__comp-img img {
  display: block;
  object-fit: cover;
  height: 100%;
}

.img__comp-overlay {
  z-index: 8;
}

.img__comp-slider {
  position: relative;
  z-index: 9;
  cursor: ew-resize;
  width: 4px;
  height: 748px;
  background-color: #2196f3;
  border-radius: 2px;
}

.intro__title {
  max-width: 600px;
  margin: 0 0 30px 0;
}

.intro__slider {
  overflow: hidden;
  background: #fff;
  width: 940px;
  margin-bottom: 30px;
}

.intro__slider-track {
  display: flex;
  justify-content: flex-start;
  transition: 1s;
  position: relative;
}

.intro__slider-item {
  height: 320px;
  margin-right: 20px;
  overflow: hidden;
}

.intro__slider-img {
  display: block;
  height: 320px;
  transition: 0.5s;
} 

.intro__slider-img:hover {
transform: scale(1.2);
}

.intro__slider-item:nth-child(1) .intro__slider-img{
  background: url("../../images/intro-slider1.png") 0 50%/auto auto no-repeat;
}

.intro__slider-item:nth-child(2) .intro__slider-img{
  background: url("../../images/intro-slider2.png") 100% 50%/auto auto no-repeat;
}

.intro__slider-item:nth-child(3) .intro__slider-img{
  background: url("../../images/intro-slider1.png") 0 50%/auto auto no-repeat;
}

.intro__slider-item:nth-child(4) .intro__slider-img{
  background: url("../../images/intro-slider2.png") 100% 50%/auto auto no-repeat;
}

.intro__slider-row {
  position: relative;
  padding-left: 130px;
  align-self: start;
  min-height: 20px;
}

.intro__text-slider {
  font-weight: 900;
  font-size: 18px;
  color: #0c59a5;
  position: relative;
  display: inline-block;
  margin-left: 30px;
}



