
@media (max-width: 1919px) {
  .header__left {
    width: 110px;
    padding-top: 30px;
  }

  .header__left-cross {
    display: none;
  }

  .header__left-cross .header__left-cross_show {
    display: none;
  }

  .header__left-item {
    min-height: 50px;
    justify-content: center;
  }

  .header__left-item:before {
    width: 45px;
    height: 45px;
  }

  
  .header__left-text {
    font-size: 12px;
  }

  .header__left-submenu-item {
    margin-bottom: 20px;
    padding: 30px;
    font-size: 16px;
  }

  .header__top-container {
    grid-template-columns: 200px 1fr 330px;
    font-size: 16px;
    gap: 30px;
  }

  .logo_place_header {
    width: 200px;
  }
  .header__top-banner-title {
    font-size: 20px;
  }  
}

@media (max-width: 1599px) {
  /* .text__title {
    font-size: 32px;
  } 

  .blue-area {
    width: 100%;
  }*/

  .header__top-container {
    grid-template-columns: 200px 1fr 270px;
    font-size: 14px;
    gap: 20px;
  }

  .header__top-banner {
    height: 65px;
  }

  .header__top-banner-title {
    font-size: 18px;
  }

  .header__top-sub-banner-img {
  height: 100px;  
}
}

@media (max-width: 1365px) {
  .header__top-container {
    grid-template-columns: 150px 1fr;
  }

  .logo_place_header {
    width: 150px;
    margin-top: -10px;
  }

  .header__top-column_place_right {
    display: none;
  }
  .header__search {
    width: 60%;
  }    
}

@media (max-width: 1023px) {
  /* .container {
    width: calc(100% - 100px);
  } */

  .header__left-item-img {
    display: none;
  } 

  .header__top {
    padding: 10px 20px;
  }

  .header__sub-menu-top-active {
    top: 0;
  }

  .header__top-column_place_center-btn {
    display: block;
    width: 50px;
    height: 50px;
    position: fixed;
    top: 20px;
    right: 20px;
    z-index: 100;
  }

  .header__top-column_place_center-btn:hover {
    cursor: pointer;
  }

  .header__top-column_place_center-btn-span {
    position: relative;
    display: block;
    width: 50px;
    height: 2px;
    background-color: #fff;
    top: 15px;
    z-index: 1;
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
    transition: 0.5s;
  }

  .header__top-column_place_center-btn-span.header__top-buerger-active {
    -webkit-transform: scale(0);
        -ms-transform: scale(0);
            transform: scale(0);
  }

  .header__top-column_place_center-btn:before,
  .header__top-column_place_center-btn:after {
    display: block;
    content: "";
    width: 50px;
    height: 2px;
    background-color: #fff;
    position: relative;
    z-index: 1;
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
    transition: 0.5s;
  }

  .header__top-column_place_center-btn:before {
    top: 0px;
  }

  .header__top-column_place_center-btn.header__top-buerger-active:before {
    -webkit-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
            transform: rotate(45deg);
    top: 15px;
  }

  .header__top-column_place_center-btn:after {
    top: 28px;
  }

  .header__top-column_place_center-btn.header__top-buerger-active:after {
    -webkit-transform: rotate(-45deg);
        -ms-transform: rotate(-45deg);
            transform: rotate(-45deg);
    top: 12px;
  }

  .header__top-column_place_center {
    position: fixed;
    top: 0px;
    right: -150px;
    width: 150px;
    height: 100vh;
    background: #0c59a5;
    -webkit-box-pack: start;
        -ms-flex-pack: start;
            justify-content: start;
    padding-top: 70px;
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
    transition: 0.5s;
  }

  .header__top-column_place_center.header__top-buerger-active {
    right: 0px;
  }

  .header__top-row1 {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    margin: 0;
    padding: 0 10px;
    width: 150px;
    background: #0c59a5;
  }

  .header__top-row1 > * {
    margin-bottom: 15px;
  }

  .header__top-button {
    background: #0c59a5;
    padding: 5px;
    text-align: center;
  }

  .header__top-city-items{
    padding: 10px;
  }


  .header__top-row2 {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    padding: 0 10px;
    min-width: 150px;
    background: #0c59a5;
  }

  .header__top-nav-item {
    margin-bottom: 20px;
  }

  .header__top-submenu-1-wrapper {
    display: none;
  }
}

@media (max-width: 767px) {
  .text__title {
    font-size: 25px;
  }

  .container {
    width: calc(100% - 50px);
  }

  .header .header__left {
    width: 100px;
  }

  .header__left-item {
    margin: 0;
  }

  .header__left-submenu-items {
    width: 200px;
    left: 100px;
  }

  .header__left-submenu-item {
    font-size: 12px;
    padding: 20px;
  }
  .header__search {
    display: none;
  }
}