.container {
  max-width: 1658px;
  margin: 0 auto;  
}

@media (max-width: 1858px) {
  .container {
    width: calc(100% - 200px);
  }      
}

@media (max-width: 1023px) {
  .container {
    width: calc(100% - 100px);
  }    
}

@media (max-width: 767px) {
  .container {
    width: calc(100% - 50px);
  }  
}
